import Header from "../component/Header";
import Footer from "../component/Footer";
import Providers from "./providers";
import "../styles/globals.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Script from "next/script";
import "../../js/aos"


export const metadata = {
  title: "Jss University",
  description: "A Next.js project with Zustand and SEO setup",
};

export default function RootLayout({ children }) {
  return (
    <html lang="en">
      <body>
        <Providers>
          <Header />
            <main className="main-container">{children}</main>
            <Script src="/js/jquery.min.js" strategy="beforeInteractive" />
            <Script src="/js/aos.js" strategy="beforeInteractive" />
            <Script src="/js/swiper-bundle.min.js" strategy="beforeInteractive" />
            <Script src="/js/smoothscroll.min.js" strategy="beforeInteractive" />
            <Script src="../js/gsap.min.js" strategy="beforeInteractive" />
            <Script src="../js/ScrollTrigger.min.js" strategy="beforeInteractive" />
            <Script src="../js/ScrollToPlugin.min.js" strategy="beforeInteractive" />
            <Script src="../js/custom.js" strategy="beforeInteractive" />
          <Footer />
        </Providers>
      </body>
    </html>
  );
}
